<template>
  <div>
    <v-data-iterator :items="edgeSevers" hide-default-footer>
      <template v-slot:default="props">
        <div v-if="selected.length > 1">
          <v-btn block @click="compareEdgeServerIps">Compare</v-btn>
        </div>
        <v-row>
          <v-col
            v-for="item in props.items"
            :key="item.country"
            cols="12"
            sm="6"
            md="4"
            lg="3"
          >
            <v-card>
              <v-card-title class="subheading font-weight-bold">
                {{ item.country }}
                <v-checkbox
                  v-model="selected"
                  :value="item.country"
                  class="ml-5"
                ></v-checkbox>
              </v-card-title>
              <v-card-subtitle>
                Length: {{ item.length }}
              </v-card-subtitle>

              <v-divider></v-divider>

              <v-list dense style="max-height: 1000px" class="overflow-y-auto">
                <v-list-item v-for="ip in item.ips" :key="ip">
                  <v-list-item-content
                    class="align-end"
                    :class="{ redtext: isIdentical(ip) }"
                  >
                    {{ ip }}
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </template>
    </v-data-iterator>
  </div>
</template>
<script>
import { getUniqueIpFromCollection } from "@/api/transactions.js";
export default {
  data() {
    return {
      edgeSevers: [],
      isLoading: false,
      selected: [],
      selectedIdenticalEdgeServers: []
    };
  },
  props: ["countryList"],
  created() {
    Promise.all(
      this.countryList
        .map(item => item.name)
        .map(country => getUniqueIpFromCollection(country))
    ).then(result => {
      this.edgeSevers = result.map((response, index) => {
        return {
          country: this.countryList[index].name,
          length: response.ips.length,
          ips: response.ips
        };
      });
    });
  },
  methods: {
    compareEdgeServerIps() {
      const vm = this;
      var data = [];
      vm.edgeSevers.forEach(item => {
        if (vm.selected.includes(item.country)) {
          data.push(item.ips);
        }
      });
      const result = data.reduce((a, b) => a.filter(c => b.includes(c)));
      vm.selectedIdenticalEdgeServers = result;
    },
    isIdentical(ip) {
      return this.selectedIdenticalEdgeServers.includes(ip);
    }
  }
};
</script>
<style lang="scss">
.redtext {
  color: red;
}
</style>