<template>
  <div>
    <v-toolbar dense class="my-3 p-1">
      <v-overflow-btn
        :items="collectionList"
        v-model="selectedCountry"
        label="Select Country"
        hide-details
        class="pr-10"
        flat
      >
      </v-overflow-btn>
      <v-toolbar-title>Format:</v-toolbar-title>
      <div class="mx-2"></div>
      <v-btn @click="selectFormat('raw')">
        Raw
      </v-btn>
      <v-btn @click="selectFormat('transaction')">
        Transaction
      </v-btn>
      <v-btn @click="selectFormat('pool')">
        Server Pool
      </v-btn>
    </v-toolbar>
    <vue-json-to-csv :json-data="ipList" :csv-title="csvTitle"
      ><v-btn block :disabled="isIpListEmpty"
        ><v-icon>mdi-download</v-icon>
        Edge Server IP List
      </v-btn>
    </vue-json-to-csv>
    <v-card class="mt-5">
      <v-data-table
        :headers="headers"
        :items="data"
        :items-per-page="20"
        :loading="isTableLoading"
        :search="search"
        :custom-filter="filterText"
        loading-text="Loading... Please wait"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-text-field
            v-model="search"
            label="Search"
            class="mx-4"
          ></v-text-field>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import {
  getAllCollections,
  getAllTransactionFromCollection,
  getUniqueIpFromCollection,
} from "@/api/transactions.js";
export default {
  data() {
    return {
      collectionList: [],
      selectedCountry: "",
      format: "raw",
      headers: [
        {
          text: "Channel",
          align: "start",
          sortable: false,
          value: "channel"
        },
        { text: "Start", value: "start" },
        { text: "End", value: "end" },
        { text: "Transaction List", value: "transactionList" },
        { text: "Server Pool", value: "serverPool" }
      ],
      search: "",
      data: [],
      isTableLoading: false,
      ipList: [],
      rawData: []
    };
  },
  created() {
    this.fetchCollectionList();
  },
  methods: {
    fetchCollectionList() {
      const vm = this;
      getAllCollections().then(response => {
        vm.collectionList = response.collections.filter(
          val => !val.match(/\d+/g)
        );
      });
    },
    selectFormat(format) {
      const vm = this;
      console.log(format);
      vm.format = format;
    },
    async fetchAllTransactionFromCollection() {
      const vm = this;
      const response = await getAllTransactionFromCollection(
        vm.selectedCountry
      );
      vm.rawData = response.transactions;
      return response.transactions;
    },
    async rawOutput() {
      const vm = this;
      vm.data = await this.rawData.map(transaction => {
        return {
          channel: transaction.channel,
          start: transaction.start,
          end: transaction.end,
          transactionList: JSON.stringify(transaction.transactionList),
          serverPool: transaction.serverPool
        };
      });
    },
    async transactionOutput() {
      const vm = this;
      let completeTransactions = [];
      vm.rawData.forEach(transaction => {
        const itemList = Object.entries(transaction.transactionList);
        const test = itemList.map(item => {
          return { channel: transaction.channel, time: item[0], ip: item[1] };
        });
        completeTransactions = completeTransactions.concat(test);
      });
      vm.data = completeTransactions;
    },
    async poolOutput() {
      const vm = this;
      vm.data = vm.rawData.map(transaction => {
        return {
          channel: transaction.channel,
          start: transaction.start,
          end: transaction.end,
          serverPool: transaction.serverPool
        };
      });
    },
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().indexOf(search) !== -1
      );
    }
  },
  computed: {
    isIpListEmpty: function() {
      return this.ipList.length == 0;
    },
    csvTitle: function() {
      return `${this.selectedCountry}_edge_server_ip_list`;
    }
  },
  watch: {
    selectedCountry: function(newValue) {
      this.isTableLoading = true;
      Promise.all([
        this.fetchAllTransactionFromCollection(),
        getUniqueIpFromCollection(newValue)
      ]).then(result => {
        this.format = "raw";
        this.ipList = result[1].ips.map(ip => {
          return { ip: ip };
        });
        this.rawOutput();
        this.isTableLoading = false;
      });
    },
    format: function() {
      switch (this.format) {
        case "raw":
          this.headers = [
            {
              text: "Channel",
              align: "start",
              sortable: false,
              value: "channel"
            },
            { text: "Start", value: "start" },
            { text: "End", value: "end" },
            { text: "Transaction List", value: "transactionList" },
            { text: "Server Pool", value: "serverPool" }
          ];
          this.rawOutput();
          break;
        case "transaction":
          this.headers = [
            {
              text: "Channel",
              align: "start",
              sortable: false,
              value: "channel"
            },
            { text: "Time", value: "time" },
            { text: "IP address", value: "ip" }
          ];
          this.transactionOutput();
          break;
        case "pool":
          this.headers = [
            {
              text: "Channel",
              align: "start",
              sortable: false,
              value: "channel"
            },
            { text: "Start", value: "start" },
            { text: "End", value: "end" },
            { text: "Server Pool", value: "serverPool" }
          ];
          this.poolOutput();
          break;
      }
    }
  }
};
</script>
<style lang="scss">
</style>