<template>
  <div class="mx-4">
    <v-row justify="space-around" class="m-3">
      <v-sheet elevation="1" height="50" width="300"
        ><v-row align="center" justify="center" dense>
          <p class="mt-3">
            <span> Number of Countries: </span
            ><span class="text-h5 text-info">{{ stats.countryNum }}</span>
          </p>
        </v-row></v-sheet
      >
      <v-sheet color="white" elevation="1" height="50" width="300">
        <v-row align="center" justify="center" dense>
          <p class="mt-3">
            Total Count:
            <span class="text-h5 text-info ">{{ stats.totalCount }}</span>
          </p>
        </v-row>
      </v-sheet>
    </v-row>
    <v-tabs v-model="tab" fixed-tabs background-color="indigo" dark>
      <v-tabs-slider color="yellow"></v-tabs-slider>
      <v-tab>
        Data Table
      </v-tab>
      <v-tab>
        Edge Servers
      </v-tab>
      <v-tab>
        Graph
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <DataTable />
      </v-tab-item>
      <v-tab-item :key="1">
        <EdgeServers :countryList="stats.countryList" />
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>
import { getDatabaseStats } from "@/api/transactions.js";
import DataTable from "./DataTable.vue";
import EdgeServers from "./EdgeServers.vue";
export default {
  data() {
    return {
      tab: null,
      stats: {
        countryNum: null,
        totalCount: null,
        countryList: []
      },
      statsInterval: undefined
    };
  },
  components: {
    DataTable,
    EdgeServers
  },
  created() {
    this.refreshDatabaseStats();
    this.statsInterval = setInterval(() => {
      this.refreshDatabaseStats();
    }, 10000);
  },
  methods: {
    refreshDatabaseStats() {
      const vm = this;
      getDatabaseStats().then(response => {
        vm.stats.countryList = response.countryList;
        vm.stats.countryNum = response.countryList.length;
        vm.stats.totalCount = response.totalCount;
      });
    }
  }
};
</script>
